<template>
    <div id="add-blog">
      <h1>添加博客</h1>
      <form v-if="! submited">
        <label>博客标题</label>
        <input type="text" v-model="blog.title" required><br/>
        <label>博客内容</label>
        <textarea v-model="blog.content" name="" id="" cols="30" rows="10"></textarea>
        <div id="checkboxes">
          <label>vue.js</label>
          <input type="checkbox" value="vue.js" v-model="blog.categores">
          <label>node.js</label>
          <input type="checkbox" value="node.js" v-model="blog.categores">
          <label>react.js</label>
          <input type="checkbox" value="react.js" v-model="blog.categores">
          <label>angular4.js</label>
          <input type="checkbox" value="angular4.js" v-model="blog.categores">
        </div>
        <label>作者：</label>
        <select v-model="blog.author">
          <option v-for="author in authors">{{author}}</option>
        </select>
        <button @click.prevent="post">添加博客</button>
      </form>
      <div v-if="submited">
        <h3>您的博客发布成功</h3>
      </div>
      <div class="preview">
        <h1>博客总览</h1>
        <p>博客标题：{{blog.title}}</p>
        <p>博客内容：</p>
        <p>{{blog.content}}</p>
        <p>分类</p>
        <ul>
          <li v-for="i in blog.categores">{{i}}</li>
        </ul>
        <p>作者:{{blog.author}}</p>
      </div>
    </div>
</template>

<script>
    export default {
      //https://jsonplaceholder.typicode.com/
        name: "add-blog",
      data(){
          return{
            blog:{
              title:'',
              content:'',
              categores:[],//存放分类的内容,
              author:'',

            },
            authors:['zhangsan','lisi','wangwu'],
            submited:false
          }
      },
      methods:{
          post:function () {
            this.$http.post("https://jsonplaceholder.typicode.com/posts",{
              title: this.blog.title,
              body:this.blog.content,
              userId:1
            })
              .then(function (data) {
                console.log(data);
                this.submited=true
              })
          }
      }
    }
</script>

<style scoped>
#add-blog *{
  box-sizing: border-box;
}
#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
  label{
    display: block;
    margin: 20px 0 10px;
  }

  input[type=text],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
  }

  textarea{
    height: 200px;
  }
  #checkboxes{
    display: inline-block;
    margin-top: 0;
  }

  #checkboxes input{
    display: inline-block;
    margin-right: 10px;
  }

  button{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: #fff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer ;
  }

  .preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
  }

  h3{
      margin-top: 10px;
  }
</style>



