<template>
    <div id="single-blog"></div>
</template>

<script>
    export default {
        name: "SingleBlog",
      data(){
          return{
            id:this.$router.params.id,
            blog:{}
          }
      },
      created() {
          this.$http.get('https://jsonplaceholder.typicode.com/posts'+this.id)
            .then(function (data) {
            this.blog=data.body
            })
      }
    }
</script>

<style scoped>
  #single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
  }
</style>



